@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Pacifico&family=Secular+One&family=Yatra+One&display=swap');

.modal-design{
    color: yellow;
    text-align: center;
}

.modal-stats{
    color: aqua;
    display: grid;
    grid-template-columns:1fr 1.5fr 1fr 1fr; 
    text-align: center;
    margin-top: 12px;
    word-wrap: wrap;
    /*verflow-y:hidden;*/color: aquamarine;
    overflow-wrap:break-word;
}

.modalstatsa{
    color: aquamarine;
    display: grid;
    grid-template-columns:1fr 1.5fr 1fr 1fr; 
    text-align: center;
    word-wrap: wrap;
    font-size: 2rem;
    margin-top: 2px; 
    padding-left: 15px;
    /*overflow-y:hidden;*/
    overflow-wrap:break-word;
}

.modalgraph{
    margin-top: 10px;
}

.but{
    color:whitesmoke;
    background-color: #444040;
    padding: 5px 15px 5px 15px;
    border-radius: 5px;
}

.but:hover{
    transform: scale(1.1);
}

.btnprimaryms-1{
    color:black;
    background-color: #5cbbd3;
    padding: 5px 15px 5px 15px;
    border-radius: 5px;
    font-family: Arial, Helvetica, sans-serif;
    cursor: pointer;
  }

  .btnprimaryms-1:hover{
    transform: scale(1.1);
}

.heading{
    color: #ffa500;
    /*font-family: 'Pacifico', cursive;*/
/*font-family: 'Secular One', sans-serif;*/
font-family: 'Yatra One', cursive;
}

h4{
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    text-shadow: 0.11px 0.11px black;
}


::placeholder{
    color: whitesmoke;
}

.answerinput{
    display: flex;
    flex-direction: row;
    gap:0;
    margin-inline-start: 5%;
}

.answerinput:hover{
    transform: scale(1.01);
}

.answe{
    border-style: solid;
    border-right: none;
    border-width: 1px;
    border-color: black;
    padding: 20px 10px 15px 10px;
    width:300px;
    display: inline;
    background-color: black;
    border-radius: 6px;
    background-image:url('../ff.avif');
    color: white;
    opacity: 80%;
  }

@media (max-width:1025px){
    .answerinput{
    margin-bottom: 20px;
    margin-inline: 10%;
    }
}

button:hover{
    transform:scale(1.05);
}