.popup-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
    display: flex;
    justify-content: center;
    align-items:center;
    z-index: 1000;
  }

  .popup{
    background-color: black;
    color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    height: fit-content;
    width: 27vw;
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    overflow-y:hidden;
    overflow-wrap:break-word;
  }

  /* Apply backdrop blur effect */
  .popup-container {
    backdrop-filter: blur(3.5px);
  }

  .chart{
    margin-inline-start: 5%;
    margin-inline-end: 5%;
  }

  .popupa-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
    display: flex;
    align-items:center;
    z-index: 1000;
  }

  .popupa{
    position: absolute;
    top:20%;
    background-image: linear-gradient(
      to bottom,
      rgba(146, 146, 69, 0.64),
      rgba(126, 79, 28, 0.64)
    ), url("../ff.avif");
    backdrop-filter: blur(4px);
    background-size: cover;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    height: fit-content;
    width: 97vw;
    display:flex;
    flex-direction: column;
    gap:0px;
    overflow-y:hidden;
    overflow-wrap:break-word;
  }

  .hint{
    display:none;
  }


  
  @media (max-width:1025px){

      .popup{
        width: 67vw;
      }

      .chart{
        margin-inline:auto;
      }
      
  
